* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@keyframes fire {
  50% {
    transform: translate(-50%, 0) scale(0.9, 1);
  }
  100% {
    transform: translate(-50%, 0) scale(1, 0.9);
  }
}

.container {
  width: 100vw;
  height: 100vh;
  background: rgb(18, 43, 71);
  background: url(../images/stars.png),
    linear-gradient(
      180deg,
      rgba(18, 43, 71, 1) 0%,
      rgba(18, 20, 37, 1) 51%,
      rgba(46, 25, 54, 1) 100%
    );
  position: relative;
  background-size: 200px 200px, cover;
  min-width: 1200px;
  min-height: 800px;
}

.rocket {
  width: 89px;
  height: 150px;
  position: absolute;
  left: 10%;
  top: 20%;
  z-index: 2;
  animation: identifier 15s ease infinite;
  @keyframes identifier {
    25% {
      position: absolute;
      top: 10%;
      left: 15%;
      transform: rotate(95deg);
    }
    50% {
      position: absolute;
      top: 20%;
      left: 85%;
      transform: rotate(140deg);
    }
    75% {
      position: absolute;
      left: 80%;
      top: 70%;
      transform: rotate(225deg);
    }
    90% {
      position: absolute;
      left: 12%;
      top: 70%;
      transform: rotate(345deg);
    }
    100% {
      position: absolute;
      left: 12%;
      top: 15%;
      transform: rotate(405deg);
    }
  }

  span {
    display: block;
    background: url(../images/rocket.png);
    width: 89px;
    height: 150px;
    position: relative;
    z-index: 1;
  }

  &.fly {
    &::after {
      content: "";
      background: url(../images/fire.png);
      background-size: cover;
      width: 42px;
      height: 67px;
      position: absolute;
      left: 50%;
      top: 87%;
      transform: translate(-50%, 0);
      animation: fire 0.1s 0s infinite alternate;
      transform-origin: 50% 0;
      z-index: 0;
    }
  }
}

.planet1 {
  position: absolute;
  background: url(../images/planet1.png);
  width: 228px;
  height: 229px;
  left: 150px;
  top: 100px;
  border-radius: 50%;
  box-shadow: 0 0 30px rgba(176, 22, 4, 0.4), 0 0 100px rgba(249, 149, 34, 0.2);
}

.planet2 {
  position: absolute;
  background: url(../images/planet2.png);
  width: 201px;
  height: 201px;
  left: calc(100% - 350px);
  top: 150px;
}

.planet3 {
  position: absolute;
  background: url(../images/planet3.png);
  width: 257px;
  height: 246px;
  left: calc(100% - 460px);
  top: calc(100% - 350px);
}

.planet4 {
  position: absolute;
  background: url(../images/planet4.png);
  width: 225px;
  height: 225px;
  left: 200px;
  top: calc(100% - 300px);
  border-radius: 50%;
  box-shadow: 0 0 50px rgba(247, 215, 76, 0.2);
}
